import React from "react";
import { Route, Switch } from "react-router-dom";

import Reader from "../Components/ReadDocs/Reader";

const MyRouters = props => {
    return (
        <Switch>
            <Route exact path="/" render = {() => <h2 className="docTitle">Select Document</h2>} />
            <Route exact path="/Reader/:id" component = {props => <Reader files={props.files} {...props} />} />
            <Route render ={() => <h1>404 NOT FOUND</h1>}/>
        </Switch>
    );
}

export default MyRouters;
